<template>
  <div class="pl-1 pr-1 mb-4 mt-10">
    <label color="primary">Detail</label>
  </div>
</template>
<script>
export default {
  name: 'ListProduct',
  data () {
    return {
      model: this.value
    }
  },
  props: {
    label: {
      type: String,
      required: false,
      default: 'Search'
    },
    items: {
      type: Array,
      required: false
    },
    appendIcon: {
      type: String,
      required: false,
      default: 'mdi-shopping-search-outline'
    },
    value: {
      required: false
    }
  },
  methods: {
    onChange () {
      this.$emit('input', this.model)
    },
    currencyRp (nominal) {
      return nominal.toFixed(0).replace(/(\d)(?=(\d{3})+(?:\.\d+)?$)/g, '$1,')
    },
    toDetail (item) {
      this.$emit('toDetail', item)
    }
  }
}
</script>
